/* ----- Description page ------------------- */
.slider-gallery {
  margin-bottom: 24px;
  &-wrapp {
    margin-bottom: 40px; }
  &-item {
    &__img {
      position: relative;
      overflow: hidden;
      min-height: 260px;
      width: 100%;
      width: 100%;
      display: block;
      text-decoration: none;
      outline: none;
      >img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        min-width: 100%;
        min-height: 100%;
        max-width: 140%; } } }
  &-thumb {
    margin-right: -4px;
    margin-left: -8px;
    &-item {
      margin-right: 8px;
      margin-left: 8px;
      &.is-active {
        .slider-gallery-thumb-item__img {
          border: 2px solid $cl-site-three;
          box-sizing: border-box;
          &:after {
            display: block; } } }

      &__img {
        position: relative;
        overflow: hidden;
        min-height: 70px;
        width: 100%;
        position: relative;
        &:after {
          content: ' ';
          display: none;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background-color: rgba(#000, 0.5); }
        > img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          min-width: 100%;
          min-height: 100%;
          max-width: 120%; } } } } }

.box-desc__cap {
  padding-top: 30px;
  position: relative;
  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: -12px;
    right: -12px;
    bottom: -100px;
    background-color: $cl-site;
    border: 1px solid $cl-site-four;
    z-index: -1; } }

.review-slider-wrapp {
  border: 1px solid $cl-site-four;
  background-color: $cl-site;
  padding: 25px 15px 5px 15px;
  margin-bottom: 40px;
  p {
    margin-bottom: 20px;
    color: $cl-light;
    font-family: $site-font-family-4;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: $lets2;
    padding-left: 5px;
    padding-right: 5px; } }

.review-slider-thumb-wrapp {
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 40px;
  .slick-dots {
    bottom: 0;
    padding-left: 85px;
    padding-right: 20px;
    bottom: auto;
    top: 100%;
    margin-top: -20px;
    text-align: left; } }

.review-personal {
  display: flex;
  justify-content: flex-start;
  &__icon {
    flex-shrink: 0;
    margin-right: 20px; }
  &__txt-bl {
    flex-grow: 1; }
  &__name {
    color: $cl-light;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    margin-bottom: 7px;
    letter-spacing: $lets2; }
  &__company {
    color: $cl-light;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0.45px;
    margin-bottom: 20px; } }

.icon-pic {
  border-radius: 50%;
  width: 70px;
  height: 70px;
  left: 70px;
  display: inline-block;
  >img {
    width: 100%;
    height: 100%; } }

.desc-grid__half-col {
  margin-bottom: 40px;
  .title-site, h1, h2, h3 {
    margin-bottom: 30px; }
  p {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.35px; } }

.desc-points {
  margin-bottom: 40px; }

.desc-point {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 5px;
  margin-bottom: 20px;
  border-bottom: 1px solid $cl-site-four;
  &__icon {
    flex-shrink: 0;
    min-width: 30px;
    margin-right: 3px; }
  &__content {
    flex-grow: 1; }
  &__tt {
    color: $cl-light;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin-bottom: 5px; }
  &__txt {
    color: #ededed;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: $lets2;
    margin-bottom: 10px; } }

.desc-doc {
  display: flex;

  &__icon {
    flex-shrink: 0;
    margin-right: 18px; }
  &__img {
    flex-grow: 1; } }
/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .slider-gallery-item__img {
    min-height: 330px; }
  .desc-points {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .desc-point {
      flex: 0 0 48%;
      max-width: 48%; } }

  .slider-gallery {
    &-thumb {
      &-item {
        &__img {
          min-height: 100px; } } } }
  /* end media min width 480 pixels */ }

/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .slider-gallery-item__img {
    min-height: 440px; }
  .review-personal {
    &__name {
      font-size: 16px; }
    &__company {
      font-size: 18px; } }

  /* end media min width 600 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .slider-gallery-item__img {
    min-height: 570px; }
  .box-desc__cap {
    padding-top: 40px; }
  .desc-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &__half-col {
      flex: 0 0 48%;
      max-width: 48%; } }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slider-gallery-item__img {
    min-height: 670px; }
  .review-slider-wrapp {
    p {
      font-size: 16px;
      line-height: 26px; } }

  .desc-grid__half-col {
    p {
      line-height: 24px;
      margin-bottom: 25px; } }

  .desc-point {
    &__icon {
      min-width: 40px; }
    &__tt {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 15px; }
    &__txt {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 15px; } }

  .review-slider-thumb-wrapp {
    padding-left: 85px; }
  .review-slider-wrapp {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: 100%;
      left: -1px;
      background: url(../img/icons/triangle-content.svg) left top no-repeat;
      width: 60px;
      height: 60px; } }
  .slider-gallery-wrapp {
    margin-bottom: 80px; }
  .desc-grid__half-col .title-site, .desc-grid__half-col h1, .desc-grid__half-col h2, .desc-grid__half-col h3 {
    margin-bottom: 41px; }
  .desc-points {
    padding-top: 3px; }
  .desc-points .desc-point {
    flex: 0 0 47%;
    max-width: 47%; }
  .desc-point {
    margin-bottom: 13px; }
  .desc-grid {
    margin-bottom: 70px; }
  .desc-grid {
    &__half-col {
      margin-bottom: 5px; } }

  .box-desc {
    background: url(../img/desc-bg.png) left bottom no-repeat; }

  /* end media min width 1000 pixels */ }

/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .box-desc__cap {
    .main-tt {
      z-index: 1; }
    &:before {
      right: -30px;
      left: -30px;
      bottom: -220px; }
    &:after {
      content: ' ';
      position: absolute;
      top: 0;
      right: -30px;
      background: url(../img/triangle-content-img.jpg) center center no-repeat;
      width: 32px;
      height: 32px;
      background-size: contain;
      z-index: -1; } }

  .slider-gallery-thumb-item__img {
    min-height: 140px; }

  .desc-grid__half-col {
    flex: 0 0 48.5%;
    max-width: 48.5%; }
  .review-slider-wrapp {
    padding-left: 35px;
    padding-right: 35px; }
  .desc-doc {
    padding-left: 10px; }
  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .box-desc__cap {
    &:before {
      right: -70px;
      left: -70px; }
    &:after {
      right: -70px;
      width: 72px;
      height: 72px; } }
  /* end media min width 1330 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {

  .box-desc__cap {
    &:before {
      right: -79px;
      left: -79px; }
    &:after {
      right: -79px;
      width: 81px;
      height: 81px; } }
  /* end media min width 1440 pixels */ }

