// colors
$cl-site: #272727;
$cl-site-second: #2b2b2b;
$cl-site-three: #c4a17b;
$cl-site-four: #3b3b3b;

$cl-light: #fff;
$cl-light-2: #ffffff;
$cl-dark: #000;
$cl-gray: #6a6a6a;
$cl-mine-shaft: #303030;
$cl-alto: #dfdfdf;
$cl-green: #00BD12;
$cl-porsche: #eab15d;
$cl-blue: #1f30ff;
$cl-scarlet: #ff2c1a;
$cl-canary: #fdfd58;
$cl-silver: #ccc;
$cl-alto: #ddd;
$cl-gallery: #eee;
$cl-silverChalice: #aaa;
$cl-tacao: #efae7c;

$grad1: linear-gradient(to right, #c6a47e 0%, #b48958 100%);

$grad1-hover: linear-gradient(to bottom, lighten(#c6a47e, 3%) 0%, darken(#b48958, 3%) 100%);

$lets1: 1.5px;
$lets2: 0.4px;
$lets3: 0.7px;
$lets4: 2.25px;

//scarlet | red || Canary | yellow

// Typography var
$site-font-style: normal;   //normal | italic | oblique | inherit
$site-font-variant: normal; //normal | small-caps | inherit
$site-font-weight: normal;
$site-fill-color: #272727;

$site-font-family: 'Lato', Arial, sans-serif;
$site-font-family-2: 'Lato', Arial, sans-serif;
$site-font-family-3: 'Lato', Arial, sans-serif;
$site-font-family-4: 'Lato', Arial, sans-serif;
$site-font-family-5: 'Lato', Arial, sans-serif;
// $site-font-family-2: 'Roboto', Arial, sans-serif
// $site-font-family-3: 'Montserrat', Arial, sans-serif
// $site-font-family-4: 'Playfair Display', serif
// $site-font-family-5: 'Geometria', Arial, sans-serif

$body-text-size: 14+(px)+/20+(px);
$site-text-size: 14+(px);
$site-line-height: 20+(px);

// $font-base: 'Roboto'
// $font-second: 'Roboto'

$site-text-color: #fff;

// Title site variables
$site-title-color: #fff;
$title-font-weight: $site-font-family;
$title-font-family: 700;
$title-line-height: 1.2+(em);

$site-h1-size-mobile: 30+(px);
$site-h2-size-mobile: 26+(px);
$site-h3-size-mobile: 24+(px);
$site-h4-size-mobile: 20+(px);
$site-h5-size-mobile: 18+(px);
$site-h6-size-mobile: 16+(px);

$site-h1-size: 34+(px);
$site-h2-size: 30+(px);
$site-h3-size: 28+(px);
$site-h4-size: 24+(px);
$site-h5-size: 20+(px);
$site-h6-size: 18+(px);


// Buttons variables
$btn-fill-color: $cl-site;
$btn-fill-hover-color: lighten($btn-fill-color, 5%);
$btn-fill-second-color: $cl-site-second;
$btn-fill-second-hover-color: lighten($btn-fill-second-color, 5%);
$btn-color: #fff;
$btn-color-hover: #fff;

$btn-radius: 5+(px);
$btn-font: normal 600 20+px+/+24+px $site-font-family;
$btn-padding: ((60-24)/2)+px 40px;
$btn-width-min: 130px;

//border btn
$btn-border: 1px solid $btn-fill-color;
$btn-border-hover-color: $btn-fill-color;
$btn-border-line-height: 22px;
$btn-border-color-text: $btn-fill-color;
$btn-border-hover-color-text: $btn-color;

//sm
$btn-padding-sm: ((30-24)/2)+px 15px;
$btn-width-min-sm: 110px;
//md
$btn-padding-md: ((50-24)/2)+px 25px;
$btn-width-min-md: 150px;
//lg
$btn-padding-lg: ((60-24)/2)+px 30px;
$btn-width-min-lg: 200px;
//font-radius-sm
$btn-size-sm: 12px;
//font-radius-md
$btn-size-md: 16px;
//font-radius-lg
$btn-size-lg: 18px;
//font-family-bold
$btn-font-family-bold: $site-font-family;
$btn-font-weight-bold: 700;
//font-family-light
$btn-font-family-light: $site-font-family;
$btn-font-weight-light: 300;
//font-radius-sm
$btn-radius-sm: 5px;

// links variables
$color-link: #c4a17b;
$color-h-link: darken($color-link, 5%);

// color .placeholder
$st-color-placeholder: $cl-gray;

// Field Forms variables Input and Textarea
$input-height: 34+(px);
$input-padding: 0 16+(px);
$input-border: 1px solid #888;
$input-font: normal 400 15px $site-font-family;
$input-color: #fff;
$input-fill-color: transparent;
$input-radius: 0px;
$input-shadow: none;

$input-hover-border-color: lighten(#888, 10%);
$input-focus-border-color: lighten(#888, 20%);

// Navigation
$burger-fill: #fff;
$burger-fill-open: $cl-light;

$font-menu-item-mobile: normal normal 20+(px)+/24+(px) $site-font-family;
$font-menu-item: normal normal 16+(px)+/20+(px) $site-font-family;
$fill-menu: $cl-site;
$fill-menu-hover: darken($cl-site, 5%);

// Field Forms textarea
$textarea-height: 130+(px);
$textarea-padding: 10+(px) 15+(px);

// breakpoint
$mob: 375px;
$mob-md: 480px;
$mob-lg: 640px;
$tabl: 768px;
$lap: 1000px;
$desk: 1200px;
$desk-md: 1600px;
$desk-lg: 1900px;

