/* ----- Animate ------------------- */

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .sl-slider {
    .slick-active.slick-current {
      animation: Slick-FastSwipeIn 1s both;

      .sl-slide__content {
        .btn {
          animation: ease-in-out fadeInUp 0.4s both 1.2s; } }
      .sl-slide__content {
        .sl-slide-txt {
          animation: ease-in-out fadeInUp 0.4s both 0.8s; } }
      .sl-slide__content {
        .title-site, h1, h2, h3, h4 {
          animation: ease-in-out fadeInUp 0.4s both 0.4s; } } } }


  .sl-slide__box {
    overflow: hidden; }

  .anim-hidden {
    opacity: 0; }

  .anim-visible {
    opacity: 1; }

  .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;

    &.infinite {
      -webkit-animation-iteration-count: infinite;
      animation-iteration-count: infinite; }

    &.delay-halfs {
      -webkit-animation-delay: 0.5s;
      animation-delay: 0.5s; }

    &.delay-300ms {
      -webkit-animation-delay: 300ms;
      animation-delay: 300ms; }

    &.delay-600ms {
      -webkit-animation-delay: 600ms;
      animation-delay: 600ms; }

    &.delay-900ms {
      -webkit-animation-delay: 900ms;
      animation-delay: 900ms; }

    &.delay-1200ms {
      -webkit-animation-delay: 1200ms;
      animation-delay: 1200ms; }

    &.delay-1s {
      -webkit-animation-delay: 1s;
      animation-delay: 1s; }

    &.delay-2s {
      -webkit-animation-delay: 2s;
      animation-delay: 2s; }

    &.fast {
      -webkit-animation-duration: 800ms;
      animation-duration: 800ms; }

    &.faster {
      -webkit-animation-duration: 500ms;
      animation-duration: 500ms; }

    &.slow {
      -webkit-animation-duration: 2s;
      animation-duration: 2s; }

    &.slower {
      -webkit-animation-duration: 3s;
      animation-duration: 3s; } }

  // show-animation bounce-in-left
  // show-animation bounce-in-right
  // show-animation fadeInUp
  // delay-300ms

  .bounce-in-left.animated {
    -webkit-animation-name: bounceInLeft;
    animation-name: bounceInLeft; }

  .bounce-in-right.animated {
    -webkit-animation-name: bounceInRight;
    animation-name: bounceInRight; }

  .fadeInLeft.animated {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft; }

  .fadeInRight.animated {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight; }

  .fadeInUp.animated {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp; }

  .zoomIn.animated {
    -webkit-animation-name: zoomIn;
    animation-name: zoomIn; }

  .pulse.animated {
    -webkit-animation-name: pulse;
    animation-name: pulse; }

  .h-social li {
    animation: fadeInLeft 1s both; }
  .menu-item {
    animation: fadeInRight 1s both; }
  .h-social li, .menu-item {
    @for $i from 1 through 7 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (150ms * $i) - 150;
        animation-delay: (150ms * $i) - 150; } } }

  .head-box-wrapp .logo_header {
    animation: jello 1s both 1s;
    background-color: $cl-site-second; }
  .head-box-wrapp_inner .logo_header {
    animation: 0s;
    background-color: $cl-site-second; }

  .about-num .animated {
    animation: ease-in-out fadeInRight 0.4s both 0.4s; }
  .main-tt {
    .title-site {
      transition: ease-in-out 0.4s; }
    .show-animation.title-site {
      text-indent: -399px; }
    .animated.title-site {
      text-indent: 0; }
    .main-tt__description_center.main-tt__description.show-animation {
      transform: translate(-50%, 100px);
      transition: ease-in-out 1s; }
    .main-tt__description_center.main-tt__description.animated {
      transform: translate(-50%, 0); } }

  .head-banner__txt-inner {
    .title-site, h1, h2 {
      animation: fadeInLeft 2s both; } }

  .skills-point.animated {
    animation: fadeInUp 1s both;
    @for $i from 1 through 7 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (150ms * $i) - 150;
        animation-delay: (150ms * $i) - 150; } } }
  @-webkit-keyframes jello {
    from,
    11.1%,
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); }

    22.2% {
      -webkit-transform: skewX(-12.5deg) skewY(-12.5deg);
      transform: skewX(-12.5deg) skewY(-12.5deg); }

    33.3% {
      -webkit-transform: skewX(6.25deg) skewY(6.25deg);
      transform: skewX(6.25deg) skewY(6.25deg); }

    44.4% {
      -webkit-transform: skewX(-3.125deg) skewY(-3.125deg);
      transform: skewX(-3.125deg) skewY(-3.125deg); }

    55.5% {
      -webkit-transform: skewX(1.5625deg) skewY(1.5625deg);
      transform: skewX(1.5625deg) skewY(1.5625deg); }

    66.6% {
      -webkit-transform: skewX(-0.78125deg) skewY(-0.78125deg);
      transform: skewX(-0.78125deg) skewY(-0.78125deg); }

    77.7% {
      -webkit-transform: skewX(0.390625deg) skewY(0.390625deg);
      transform: skewX(0.390625deg) skewY(0.390625deg); }

    88.8% {
      -webkit-transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
      transform: skewX(-0.1953125deg) skewY(-0.1953125deg); } }

  @keyframes jello {
    from,
    11.1%,
    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); }

    22.2% {
      -webkit-transform: skewX(-12.5deg) skewY(-12.5deg);
      transform: skewX(-12.5deg) skewY(-12.5deg); }

    33.3% {
