/* ----- Main About ------------------- */
a.img-br {
  &:after {
    transition: ease-in-out 0.15s; }

  &:hover {
    &:after {
      left: 0;
      right: 0;
      bottom: 0;
      top: 0; } } }

.img-br {
  max-width: 400px;
  background-color: #2c2319;
  position: relative;
  z-index: 1;
  display: block;
  text-decoration: none;
  outline: none;
  overflow: hidden;
  display: inline-block;

  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(#2c2319, 0.3); }

  &:after {
    content: ' ';
    position: absolute;
    top: 10px;
    right: 10px;
    bottom: 10px;
    left: 10px;
    border: 1px solid $cl-light; } }

.main-video {
  display: inline-block;
  &__link {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-decoration: none;
    outline: none;
    color: $cl-light;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: $lets2;
    &:hover {
      .main-video__name {
        color: $cl-site-three; } }
    .icon {
      flex-shrink: 0;
      margin-right: 18px; } }
  &__tt {
    margin-bottom: 10px;
    color: $cl-light; }
  &__name {
    color: $cl-light;
    font-size: 20px;
    line-height: 22px;
    text-transform: uppercase;
    letter-spacing: 0.5px; } }

.main-about-tt {
  color: $cl-light;
  font-size: 28px;
  font-weight: 800;
  line-height: 38px;
  letter-spacing: $lets3;
  margin-bottom: 20px; }

.main-about-icons {
  li {
    margin-bottom: 20px; }
  .icon {
    position: relative;
    left: -10px; }
  &__name {
    color: #f7f7f7;
    margin-top: -5px;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.35px; } }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .img-br {
    &:after {
      top: 25px;
      right: 25px;
      bottom: 25px;
      left: 25px; } }
  .main-about {
    .main-video {
      margin-top: 14px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-about {
    .img-br {
      min-height: 503px;
      width: 100%;
      >img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        min-width: 100%;
        min-height: 100%;
        max-width: none;
        z-index: -1; } } }

  .main-about-tt {
    margin-bottom: 31px; }
  /* end media min width 1000 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .main-about .img-br {
    min-width: 430px; }
  /* end media min width 1330 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .main-about .img-br {
    min-width: 490px; }
  /* end media min width 1440 pixels */ }


/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .main-about .img-br {
    min-width: 560px; }
  /* end media min width 1600 pixels */ }


/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .main-about .img-br {
    min-width: 660px; }
  /* end media min width 1800 pixels */ }


/* ----- END Main About --------------- */
