/* ----- About page ------------------- */

.st-video {
  display: inline-block;
  vertical-align: top;
  position: relative;
  text-decoration: none;
  outline: none;
  max-width: 400px;
  &:hover {
    &:before {
      transform: scale(1.1); } }
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    background: url(../img/icons/play2.png) center center no-repeat;
    background-size: contain;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
    transition: ease-in-out 0.2s; } }

.about-num {
  display: flex;
  margin-bottom: 2px;
  &__value {
    color: #7d7d7d;
    font-size: 52px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 2.18px;
    flex-shrink: 0;
    padding-top: 33px;
    padding-right: 14px;
    margin-right: 124px;
    position: relative;
    height: 75px;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 100%;
      width: 124px;
      height: 1px;
      background-color: #47484a; } }
  &__icon {
    position: relative;
    left: -5px;
    &_first {
      position: relative;
      left: -38px; } } }

.about-block {
  margin-bottom: 40px;
  p {
    color: $cl-light;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: $lets2; }
  &__txt {
    margin-bottom: 30px; } }

.about-slider-item__img {
  min-height: 300px;
  width: 100%;
  position: relative;
  overflow: hidden;
  display: block;
  text-decoration: none;
  outline: none;
  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: none; } }

.banner-sm {
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(../img/banner-sm.jpg);
  background-color: #1d1e17;
  background-size: cover;
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 300px;
  text-align: center;
  &__txt-bl {
    padding: 40px 0 10px; }
  &__name {
    color: $cl-light;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.1;
    letter-spacing: 1.8px;
    margin-bottom: 30px; }
  &__tt {
    color: $cl-light;
    font-size: 20px;
    font-family: $site-font-family-4;
    font-weight: 700;
    line-height: 1.1;
    text-transform: uppercase;
    letter-spacing: $lets1;
    margin-bottom: 30px; } }

.section-st_about-bg-bt {
  background: url(../img/about-bg-bt.png) center 101% no-repeat;
  background-size: 1000px auto; }
.section-st_about-bg-bt.section-st {
  padding-bottom: 100px; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .st-video {
    &:before {
      width: 96px;
      height: 96px;
      margin-top: -48px;
      margin-left: -48px; } }

  .about-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    &__txt {
      flex: 0 0 48%;
      max-width: 48%;
      margin-bottom: 0;
      .title-site, h1, h2, h3 {
        letter-spacing: $lets3; } }
    &__slider {
      flex: 0 0 48%;
      max-width: 48%; } }

  .about-block__slider {
    .slick-prev {
      margin-top: -75px;
      &:before {
        left: 0; } }
    .slick-next {
      margin-top: 0;
      right: auto;
      left: 0;
      &:before {
        left: 0px; } } }

  .about-slider-item__img {
    min-height: 400px; }

  .about-block_rotate {
    .about-block__txt {
      order: 1; }
    .slick-prev {
      left: auto;
      right: 0; }
    .slick-next {
      right: 0;
      left: auto; } }

  .about-num {
    min-height: 100px; }

  .banner-sm {
    min-height: 380px;
    &__name {
      font-size: 36px;
      margin-bottom: 40px; }
    &__tt {
      font-size: 30px; } }

  .section-st_about-bg-bt {
    background-size: 100% auto;
    background-position: 50% 101%; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .about-block {
    p {
      font-size: 16px;
      line-height: 28px; } }
  .about-block {
    align-items: stretch;
    margin-bottom: 70px; }
  .about-slider-item__img, .about-wrapp-slider, .slick-slider, .slick-track, .slick-list, .slick-slide, .about-slider-item {
    height: 100%; }
  .about-block__slider {
    padding: 0 0 10px; }
  .about-block__txt {
    margin-bottom: 0; }
  .section-st_about-bg-bt.section-st {
    padding-bottom: 150px; }


  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .about-block {
    margin-bottom: 90px; }
  .about-block_rotate {
    .about-block__txt {
      flex: 0 0 41%;
      max-width: 41%; }
    .about-block__slider {
      flex: 0 0 52%;
      max-width: 52%; } }
  .about-block__txt {
    flex: 0 0 44%;
    max-width: 44%;
    h3, h1, h2, .title-site {
      margin-bottom: 37px; }
    p {
      margin-bottom: 30px; } }
  .about-block__slider {
    flex: 0 0 51%;
    max-width: 51%; }

  .section-st_about-bg-bt {
    background-position: 50% 102%; }

  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .about-wrapp-slider {
    margin-right: -40px; }
  .about-block_rotate {
    .about-wrapp-slider {
      margin-right: 0;
      margin-left: -40px; } }
  /* end media min width 1330 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .about-wrapp-slider {
    margin-right: -100px; }
  .about-block_rotate {
    .about-wrapp-slider {
      margin-left: -100px; } }
  /* end media min width 1440 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .about-wrapp-slider {
    margin-right: -185px; }
  .about-block_rotate {
    .about-wrapp-slider {
      margin-left: -185px; } }
  /* end media min width 1600 pixels */ }

/* ----- END About page --------------- */
