/* ----- maps ------------------- */
.main-map-bl {
  position: relative;
  overflow: hidden; }

.main-map {
  min-height: 400px; }

.main-map-bl__adr {
  box-shadow: 0 -1px 24px rgba(0, 0, 0, 0.19);
  background-color: $cl-light;
  color: #1b1b1b;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.5px;
  padding: 10px 15px; }

// /* Google MAP hidden nav */
.gm-style-cc {
  display: none !important; }


/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .main-map-bl__adr {
    font-size: 20px;
    line-height: 28px;
    padding: 20px 20px; }
  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .main-map-bl__adr {
    position: absolute;
    top: 0;
    right: 0; }

  .main-map {
    min-height: 460px; }
  /* end media min width 768 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {

  .main-map-bl__adr {
    position: absolute;
    top: 100px;
    right: 150px; }
  /* end media min width 1200 pixels */ }


/* ----- END maps --------------- */
