
/* Slider */
/* Navigation */
$owl-nav-wh: 40px;
$owl-nav-arr-w: 8;
$owl-nav-arr-h: 12;
$owl-nav-arr-left: url(../img/icons/arrow-left-slider.svg);
$owl-nav-arr-right: url(../img/icons/arrow-right-slider.svg);

.slick-slide {
  outline: none; }
// arrow

.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  border: none;
  outline: none;
  background: transparent;
  margin: 0;
  padding: 0; }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }


.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }


[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto; }


[dir='rtl'] .slick-next {
  right: auto;
  left: -25px; }

.slick-prev, .slick-next {
  width: 40px;
  height: 40px;
  margin-top: -20px;
  background: #2e2c2c;
  position: absolute;
  top: 50%;
  z-index: 1;
  padding: 0;
  border: none;
  outline: none;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto; }
  &:hover {
    background-color: #d1a27a; } }

.slick-prev {
  left: 0;
  &:before {
    right: 4px;
    background: url(../img/icons/arrow-left-slider-sm.png) center center no-repeat; } }
.slick-next {
  right: 0;
  &:before {
    left: 4px;
    background: url(../img/icons/arrow-right-slider-sm.png) center center no-repeat; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .slick-prev, .slick-next {
    width: 75px;
    height: 75px;
    margin-top: -37px; }
  /* end media min width 768 pixels */ }


.dots-slider-sm {
  .slick-prev, .slick-next {
    background-color: transparent;
    box-shadow: none;
    border-radius: 0px;
    &:before {
      width: 8px;
      margin-top: -10px;
      height: 20px; } }
  .slick-prev {
    left: -15px;
    &:before {
 } }      // background-image: url(../img/icons/arrow-left-slider-sm.svg)
  .slick-next {
    right: -15px;
    &:before {
 } } }      // background-image: url(../img/icons/arrow-right-slider-sm.svg)

.sl-arrow {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.nav-st {
  .main-slick-prev, .main-slick-next {
    position: absolute;
    top: 50%;
    z-index: 1;
    margin-top: -11px;
    &:hover {
      color: #fff; } }
  .main-slick-prev {
    left: 5px;
    &__icon {
      background-image: url(../img/icons/arrow-left-light.png); } }
  .main-slick-next {
    right: 5px;
    &__icon {
      background-image: url(../img/icons/arrow-right-light.png); } } }


.main-slick-prev, .main-slick-next {
  color: $cl-light;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  background: none;
  border: none;
  box-shadow: none;
  position: relative;
  outline: none;
  &:hover {
    color: #8a8a8a;
    background: none;
    border: none;
    box-shadow: none;
    outline: none; }
  &__icon {
    position: absolute;
    top: 50%;
    margin-top: -8px;
    width: 21px;
    height: 16px;
    display: inline-block;
    vertical-align: middle; } }

.main-slick-prev {
  padding-left: 37px;
  margin-right: 10px;
  &__icon {
    background: url(../img/icons/arrow-left.png) center center no-repeat;
    left: 0; } }

.main-slick-next {
  padding-right: 37px;
  &__icon {
    background: url(../img/icons/arrow-right.png) center center no-repeat;
    right: 0; } }


/* Dots */
$owl-dot-wh: 12;
$owl-dot-margin: 6;
$wh-dots: $owl-dot-wh+($owl-dot-margin*2);

.slick-dots {
  position: absolute;
  bottom: -30px;
  width: 100%;
  text-align: center;

  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 $owl-dot-margin+(px);
    padding: 0;
    cursor: pointer;

    button {
      font-size: 0;
      line-height: 0;
      display: inline-block;
      position: relative;
      background-color: #ebebeb;
      width: 32px;
      height: 2px;
      padding: 0;
      margin: 0;
      border: none;
      box-shadow: none;
      outline: none;


      &:hover:before, &:focus:before {
        opacity: 1; }

      &:before {
        content: ' ';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 40px;
        height: 30px;
        margin-top: -15px;
        margin-left: -20px;
        background-color: transparent; } }


    &.slick-active button {
      background-color: $cl-site-three; } } }

.dots-st {
  .slick-dots {

    li {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      margin: 0 $owl-dot-margin+(px);
      padding: 0;
      cursor: pointer;

      button {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: rgba($cl-light, 0.2);

        &:before {
          width: 14px;
          height: 14px;
          margin-top: -7px;
          margin-left: -7px; } }


      &.slick-active button {
        background-color: $cl-light; } } } }
