/* ----- Contacts ------------------- */

.contact-info__cap {
  color: $cl-light;
  font-size: 28px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: $lets3;
  margin-bottom: 10px; }

.contact-info__txt {
  color: $cl-light;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: $lets2;
  margin-bottom: 20px; }

.contact-info__point {
  font-size: 16px;
  letter-spacing: $lets2;
  line-height: 20px;
  margin-bottom: 20px;
  a {
    display: inline-block;
    padding-left: 23px;
    color: $cl-light;
    text-decoration: none;
    outline: none;
    position: relative;
    &:hover {
      text-decoration: none;
      color: $cl-site-three; }
    .fa {
      color: $cl-site-three;
      font-size: 14px;
      position: absolute;
      top: 3px;
      left: 0; } } }

.contact-info-social {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 30px;
  &__list {
    display: flex;
    align-items: center;
    li {
      margin-right: 12px;
      &:last-child {
        margin-right: 0; } }
    a {
      &:hover {
        opacity: 0.6; } } }
  &__txt {
    color: $cl-light;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: $lets2;
    margin-right: 15px; } }

.contact-info__logo {
  text-align: center;
  margin-bottom: 30px;
  a {
    display: inline-block; } }

.section-contact {
  padding-top: 40px; }

.contact-info__subtt {
  display: none; }

.contact-grid__form {
  margin-left: -15px;
  margin-right: -15px;
  padding: 20px 15px 50px;
  background: url(../img/contact-bg-form.png) center bottom no-repeat; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .contact-grid {
    display: flex;
    justify-content: space-between;
    &__info {
      width: 33.3%; }
    &__form {
      width: 66.4%;
      padding: 50px 30px 70px;
      margin-left: 0;
      margin-right: 0;
      background-color: $cl-site;
      border-top: 1px solid $cl-site-four; } }
  .contact-info {
    &__cap {
      padding: 0 15px 20px;
      margin-bottom: 20px;
      border-bottom: 1px solid $cl-site-four; }
    &__body {
      padding: 0 15px 20px; } }
  .contact-info__txt {
    font-size: 14px;
    line-height: 20px; }
  .contact-info__point {
    font-size: 14px; }
  .contact-info__point a .fa {
    top: 2px; }
  .contact-info-social {
    flex-wrap: wrap;
    margin-bottom: 100px; }
  .contact-info-social__list li {
    margin-right: 10px; }
  .contact-info-social__txt {
    font-size: 14px; }
  .contact-info__subtt {
    display: block;
    color: $cl-mine-shaft;
    font-size: 41px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: $lets1;
    margin-bottom: 40px; }
  .section-contact_push-top.section-contact {
    padding-top: 100px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .contact-info__subtt {
    font-size: 54px; }
  .contact-grid__form {
    padding: 44px 40px 70px; }

  .form-contact__cap .form-contact__tt {
    margin-bottom: 5px; }
  .contact-info__txt {
    font-size: 16px;
    line-height: 28px;
    margin-bottom: 40px; }
  .contact-info__cap {
    margin-bottom: 14px; }
  .contact-info__point {
    font-size: 16px;
    margin-bottom: 28px; }
  .contact-info-social__txt {
    font-size: 16px; }
  .contact-info-social__list li {
    margin-right: 14px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .contact-info__subtt {
    font-size: 60px; }
  .contact-grid__form {
    padding: 44px 70px 90px; }
  .contact-info-social__txt {
    margin-right: 18px; }
  .contact-info-social__list li {
    margin-right: 26px; }
  .contact-info__logo {
    padding-right: 50px; }
  .contact-grid {
    justify-content: flex-start; }
  .contact-grid__info {
    width: 33.4%; }
  .contact-grid__form {
    width: 66.6%;
    border-right: 1px solid $cl-site-four;
    border-left: 1px solid $cl-site-four; }
  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .contact-info__subtt {
    position: relative;
    left: -66px; }
  /* end media min width 1330 pixels */ }

/* ----- END Contacts --------------- */
