/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.18);
  background-color: #c6a47e;
  background-image: $grad1;
  border-radius: 30px;
  .icon_arrow-btn-next {
    position: relative;
    left: 0;
    transition: ease-in-out 0.2s; }
  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: $btn-fill-hover-color;
    background-image: $grad1-hover;

    .icon_arrow-btn-next {
      left: 7px; } }

  &_second-st {
    background-color: $btn-fill-second-color;

    &:hover {
      background-color: $btn-fill-second-hover-color; } }

  &_border {
    border: $btn-border;
    color: $btn-border-color-text;
    line-height: $btn-border-line-height;
    background: transparent;

    &:hover {
      color: $btn-border-hover-color-text;
      background-color: $btn-border-hover-color; }

    &.btn_second-st {
      border-color: $btn-fill-second-color;
      color: $btn-fill-second-color;

      &:hover {
        color: $btn-border-hover-color-text;
        background-color: $btn-fill-second-color; } } }

  &_min-width {
    min-width: 250px; }

  &_max {
    width: 100%; }

  &_sm {
    padding: $btn-padding-sm;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_md {
    padding: $btn-padding-md;

    &.btn_min-width {
      min-width: $btn-width-min-md; } }

  &_lg {
    padding: $btn-padding-lg;

    &.btn_min-width {
      min-width: $btn-width-min-lg; } }

  &__icon-text {
    display: inline-block;
    vertical-align: middle; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;

    &_right {
      margin-left: 23px; }

    &_left {
      margin-right: 23px; } }

  &_icon_sm {
    padding-left: 24px;
    padding-right: 24px;
    .btn__icon_right {
      margin-left: 12px; }
    .btn__icon_left {
      margin-right: 12px; } }

  &_refresh {
    .btn__icon {
      transform: rotate(0);
      transition: ease-in-out 0.2s; }
    &:hover {
      .btn__icon {
        transform: rotate(300deg); } } } }

.btn-st {
  background: none;
  padding: 0;
  border: none;
  box-shadow: none;
  padding-right: 38px;
  position: relative;
  color: #fff;
  color: $cl-light;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.5px;
  padding-left: 20px;
  cursor: pointer;
  &:after {
    content: ' ';
    position: absolute;
    top: 50%;
    right: 0;
    width: 18px;
    height: 14px;
    margin-top: -7px;
    background: url(../img/icons/arrow-btn-next.png) center center no-repeat;
    transition: ease-in-out 0.2s; }
  &:hover {
    &:after {
      right: -5px; } } }


input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: 60px;
  width: auto;
  &_min-width {
    min-width: 250px; }
  &_max {
    width: 100%; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;
  letter-spacing: 0.38px;
  border-left: none;
  border-top: none;
  border-right: none;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }

/* ----- END controls ------------------- */
