/* ----- Customers ------------------- */
.custom-grid__content {
  margin-bottom: 40px; }

.custom-grid {
  margin-bottom: 40px; }

.list-brand {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: 40px;
  &__item {
    flex: 0 0 50%;
    padding-left: 10px;
    padding-right: 10px;
    margin: 30px 0;
    text-align: center; } }

.banner-review {
  border-top: 1px solid $cl-site-four;
  border-bottom: 1px solid $cl-site-four;
  padding: 60px 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: $cl-site url(../img/review-bg.png) center bottom no-repeat;
  min-height: 340px;
  text-align: center; }

.review-slider-st {
  padding-bottom: 20px; }

.review-slider-st-item {
  p {
    color: $cl-light;
    font-family: $site-font-family-4;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.45px;
    max-width: 1030px;
    margin-left: auto;
    margin-right: auto;
    min-height: 140px; }
  span {
    color: $cl-light;
    font-weight: 600;
    font-size: 14px;
    letter-spacing: 0.45px;
    line-height: 20px;
    display: inline-block; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .custom-grid {
    display: flex;
    margin-bottom: 80px;
    &__content {
      flex: 0 0 400px;
      max-width: 400px;
      margin-right: 40px; }
    &__img {
      flex-grow: 1; } }

  .img-wrapp {
    position: relative;
    overflow: hidden;
    min-height: 300px;
    width: 100%;
    display: block;
    >img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      max-width: 230%; } }

  .list-brand {
    &__item {
      flex: 0 0 33.3%; } }

  .custom-grid__content {
    p {
      &:last-child {
        margin-bottom: 0; } } }

  .review-slider-st-item {
    p {
      font-size: 18px;
      line-height: 30px; }
    span {
      font-size: 18px;
      line-height: 24px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .custom-grid {
    margin-bottom: 140px; }

  .list-brand {
    justify-content: center;
    &__item {
      flex: 0 0 22%;
      margin-bottom: 46px; } }

  .custom-box__brand {
    .title-site {
      &:before,
      &:after {
        width: 150px; } } }

  .custom-box__tt-wrapp {
    .main-tt__description {
      font-size: 70px;
      top: 50%;
      transform: translateY(-50%);
      bottom: auto; }
    &.main-tt {
      padding-left: 200px; }
    &.main-tt_line-right {
      .title-site, h1, h2, h3 {
        &:after {
          margin-left: 50px;
          width: 120px; } } } }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .custom-grid {
    display: flex;
    &__content {
      flex: 0 0 490px;
      max-width: 490px;
      margin-right: 50px;
      margin-bottom: 0; } }
  .custom-grid__img {
    position: relative;
    &:after {
      position: absolute;
      content: ' ';
      left: 108px;
      right: -20px;
      top: -35px;
      bottom: -35px;
      border: 1px solid $cl-site-four;
      z-index: -1; } }

  .img-wrapp {
    min-height: 316px; }
  .custom-box__tt-wrapp.main-tt {
    padding-left: 180px; }
  .custom-box__tt-wrapp.main-tt_line-right .title-site:after, .custom-box__tt-wrapp.main-tt_line-right h1:after, .custom-box__tt-wrapp.main-tt_line-right h2:after, .custom-box__tt-wrapp.main-tt_line-right h3:after {
    margin-left: 50px;
    width: 220px;
    right: 40px; }
  .custom-box .main-tt__description {
    font-size: 90px; }
  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .custom-grid__img {
    .img-wrapp {
      min-width: 670px; } }
  /* end media min width 1330 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .custom-grid__img {
    .img-wrapp {
      min-width: 720px; } }
  /* end media min width 1440 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .custom-grid__img {
    .img-wrapp {
      min-width: 750px; } }
  .custom-grid__img:after {
    right: -70px; }
  /* end media min width 1600 pixels */ }

