/* ----- Doc page ------------------- */

.doc-block__txt {
  margin-bottom: 30px;
  &_center {
    text-align: center;
    margin: 0 auto 30px;
    max-width: 830px; } }

.slider-doc-thumb-item {
  text-align: center;
  border: 2px solid transparent;
  &.is-active {
    position: relative;
    border-color: $cl-site-three;
    &:before {
      content: ' ';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: rgba($cl-site, 0.47); } }
  img {
    width: 100%; } }

.slider-doc {
  padding: 0 50px;
  margin-bottom: 30px;
  &-item {
    text-align: center;
    display: block;
    img {
      display: inline-block; } } }


.slider-doc-thumb {
  margin-bottom: 30px;
  padding-left: 20px;
  padding-right: 20px;
  .slick-track {
    display: flex;
    align-items: center; }
  .slick-slide {
    float: none;
    margin-right: 10px;
    margin-left: 10px;
    flex-shrink: 0; } }


.slider-doc-thumb {
  .slick-prev, .slick-next {
    background-color: transparent;
    &:hover {
      background-color: $cl-site-three; } } }

.slider-doc-wrapp {
  .slick-prev {
    left: -15px; }
  .slick-next {
    right: -15px; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .slider-doc {
    &-item {
      img {
        max-width: 350px; } } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .slider-doc-thumb {
    padding-left: 80px;
    padding-right: 80px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .slider-doc {
    padding-left: 100px;
    padding-right: 100px;
    margin-bottom: 100px;
    &-item {
      img {
        max-width: 600px;
        max-height: 600px; } } }
  .doc-block__txt {
    margin-bottom: 60px; }
  .slider-doc-thumb {
    margin-bottom: 60px;
    .slick-prev {
      &:before {
        background-image: url(../img/icons/arrow-left-slider-md.png);
        width: 15px;
        height: 30px; } }
    .slick-next {
      &:before {
        background-image: url(../img/icons/arrow-right-slider-md.png);
        width: 15px;
        height: 30px; } } }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-doc {
    padding-left: 200px;
    padding-right: 200px;
    .slick-prev {
      left: 90px; }
    .slick-next {
      right: 90px; } }

  .slider-doc-thumb {
    padding-left: 88px;
    padding-right: 88px;
    &-item {
      margin-right: 12px;
      margin-left: 12px; }
    .slick-prev {
      left: 30px; }
    .slick-next {
      right: 30px; } }
  /* end media min width 1200 pixels */ }

