/* ----- Brands ------------------- */
.brand-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: -2%;
  margin-right: -2%;
  margin-bottom: 40px;
  &-item {
    width: 46%;
    margin: 20px 2%;
    text-align: center; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .brand-list {
    &-item {
      width: 29.3%; } }
  /* end media min width 480 pixels */ }



/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .brands-grid {
    display: flex;
    justify-content: space-between;
    &__points {
      flex: 0 0 500px;
      max-width: 500px;
      margin-right: 30px; }
    &__txt {
      flex-grow: 1; } }

  .brand-list {
    &-item {
      margin-top: 0;
      margin-bottom: 60px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .brands-grid {
    &__points {
      flex: 0 0 610px;
      max-width: 610px;
      margin-right: 60px; } }

  .brand-list-item {
    margin-bottom: 75px;
    &:nth-last-child(-n+3) {
      margin-bottom: 0; } }
  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .section-st_brands-wrapp {
    .main-tt__description {
      left: -160px;
      bottom: -40px; } }
  /* end media min width 1330 pixels */ }


/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .section-st_brands-wrapp .main-tt__description {
    left: -200px; }
  /* end media min width 1600 pixels */ }

/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .section-st_brands-wrapp .main-tt__description {
    left: -255px; }
  /* end media min width 1800 pixels */ }

