/* ----- Header ------------------------ */
.header {
  position: relative;
  padding: 15px 0; }

.logo {
  a {
    display: block;
    outline: none; }

  &_header {
    max-width: 130px; } }

.select-sm {
  position: relative;
  display: inline-block;
  &__name {
    padding-top: 2px; }
  &__img {
    width: 19px;
    height: 12px;
    flex-shrink: 0;
    margin-right: 10px;
    line-height: 12px;
    >img {
      width: 100%;
      height: 100%; } }
  &__cap {
    position: relative;
    display: block;
    color: #fff;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 14px;
    line-height: 14px;
    font-weight: 400;
    text-transform: uppercase;
    padding: 5px 20px 5px 5px;
    cursor: pointer;
    .fa {
      font-size: 8px;
      color: #fff;
      position: absolute;
      top: 8px;
      right: 6px;
      transition: ease-in-out 0.15s; } }

  &-dd {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    min-width: 100%;
    background-color: $cl-site-second;
    box-shadow: 2px 2px 5px rgba(#000, 0.3);
    border-radius: 3px;
    overflow: hidden;
    opacity: 0;
    z-index: 102;
    animation: {
      name: menu-anim-select;
      duration: 150ms;
      timing-function: ease-in-out;
      fill-mode: forwards; }

    &__point {
      display: flex;
      align-items: center;
      text-decoration: none;
      outline: none;
      color: #fff;
      padding: 2px 5px;
      &:hover {
        color: #fff;
        background-color: lighten($cl-site-second, 5%); }
      .select-sm__name {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis; } } } }

@keyframes menu-anim-select {
  0% {
    opacity: 0;
    transform: scale(0.3) translateY(-60px); }
  30% {
    opacity: 1; }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0); } }

.header-mail {
  position: relative;
  display: inline-block;
  padding-left: 24px;
  color: $cl-light;
  font-size: 14px;
  line-height: 18px;
  font-weight: 300;
  text-decoration: none;
  letter-spacing: 0px;
  &:hover {
    color: $cl-site-three;
    text-decoration: none; }
  .fa {
    position: absolute;
    color: $cl-site-three;
    left: 0;
    top: 1px; } }

.header-cap-info__item_mail {
  display: none; }

.header-body-info__phone {
  display: none; }

.header-inner__wrapp-logo {
  float: left; }

.header-inner__info {
  margin-left: 130px; }

.select-sm {
  &.click--add-class {
    .select-sm-dd {
      display: block; }
    .select-sm__cap {
      .fa {
        transform: rotate(180deg);
        top: 11px; } } } }

.header-cap-info {
  text-align: right; }

.sl-slide__box {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #141e1b;
  position: relative;
  overflow: hidden;
  z-index: 1;
  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(#141e1b, 0.45);
    z-index: -1; }
  &_bg55 {
    &:before {
      background-color: rgba(#141e1b, 0.55); } }
  &_bg58 {
    &:before {
      background-color: rgba(#141e1b, 0.58); } }
  &_bg60 {
    &:before {
      background-color: rgba(#141e1b, 0.60); } }
  &_bg64 {
    &:before {
      background-color: rgba(#141e1b, 0.64); } } }

.head-box-wrapp {
  background-color: $cl-site-second; }

.head-box-banner__social {
  padding-bottom: 10px; }

.h-social {
  position: relative;
  padding: 10px 90px 10px 0;
  display: inline-block;
  &:before {
    content: ' ';
    width: 70px;
    background-color: #929292;
    height: 1px;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -1px; }
  li {
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
    a {
      &:hover {
        opacity: 0.7; } } } }

.sl-slide__content {
  padding: 70px 15px;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 500px; }

.sl-slide-txt {
  color: $cl-light;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.45px;
  margin-bottom: 30px; }

.sl-slide-txt__shadow {
  position: relative;
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url(../img/shadow-txt.png) center center no-repeat;
    width: 758px;
    height: 573px; } }

.sl-slider-wrapp {
  position: relative; }

.head-banner__icon-next {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer; }


.slideshow-pager {
  font-family: $site-font-family-2;
  font-weight: normal;
  &__value {
    display: inline-block;
    vertical-align: top;
    color: $cl-site-three;
    font-size: 50px;
    font-weight: 400;
    line-height: 80px; }
  &__all {
    display: inline-block;
    vertical-align: top;
    color: #929292;
    font-size: 24px;
    font-weight: 500;
    line-height: 43.7px;
    letter-spacing: 0.33px; } }

.nav-sl-slider {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px; }

.head-banner_bg {
  background-position: center center;
  background-color: #040505;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1;
  &_shadow {
    &:before {
      content: ' ';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: rgba(#040505, 0.6);
      z-index: -1; } } }

.head-banner__txt-inner {
  min-height: 250px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 50px 15px;
  .title-site, h1, h2, h3 {
    margin-bottom: 0; } }

.head-box-banner__social_mob-hidden {
  display: none; }

.head-banner__txt-inner_shadow {
  background: url(../img/shadow-txt-inner.png) center center no-repeat;
  background-size: 200px 200px; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .header-cap-info__item {
    padding-right: 15px;
    margin-right: 19px;
    border-right: 1px solid #ebebeb;
    line-height: 18px;
    &:last-child {
      padding-right: 0;
      margin-right: 0;
      border: none; } }
  .header-cap-info__item_mail {
    display: block;
    padding-right: 23px; }
  .header-cap-info {
    display: flex;
    align-items: center;
    justify-content: flex-end; }
  .sl-arrow {
    .main-slick-prev {
      margin-right: 30px; } }

  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
