      line-height: 44px; } }

  .head-box-banner__social {
    min-height: 600px;
    display: flex;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    padding-bottom: 1px;
    padding-top: 1px;
    .container {
      width: 100%; } }

  .h-social {
    padding-right: 0;
    text-align: center;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 170px;
    padding-top: 120px;
    &:before {
      left: 15px;
      top: 0;
      bottom: auto;
      right: auto;
      height: 89px;
      width: 1px; }
    li {
      display: block;
      text-align: center;
      margin-right: 0px;
      width: 30px;
      text-align: center;
      margin: 0 0 20px; }
    a {
      display: inline-block;
      vertical-align: middle; } }

  .head-box-banner {
    position: relative;
    min-height: 600px;
    &__social {
      flex-shrink: 0; }
    .head-banner {
      position: absolute;
      top: -20px;
      left: 60px;
      right: 0;
      bottom: 0; }
