  .sl-slide__content {
    min-height: 100px;
    text-align: right;
    align-items: flex-end;
    padding: 100px 30px 100px 145px; }
  .nav-sl-slider {
    position: absolute;
    top: 0;
    left: -60px;
    display: block;
    background-color: $cl-site-second;
    padding-top: 20px;
    padding-right: 20px;
    padding-bottom: 15px; }

  .logo_header {
    position: relative;
    z-index: 10; }

  .head-banner__icon-next {
    bottom: 35px; }

  .head-box-wrapp_inner {
    .head-box-banner {
      min-height: 400px; }
    .head-box-banner__social {
      min-height: 400px; }
    .h-social {
      margin-top: 20px;
      text-align: center;
      max-width: 135px;
      &:before {
        left: 50%; }
      li {
        margin-left: auto;
        margin-right: auto; } }
    .head-banner {
      left: 150px; } }
  .head-banner__txt-inner {
    height: 100%;
    align-items: flex-end;
    text-align: right; }
  .title-site_h1_big, .title-site_h1_md {
