  .head-banner__txt-inner .title-site, .head-banner__txt-inner h1, .head-banner__txt-inner h2, .head-banner__txt-inner h3 {
    margin-bottom: 30px;
    margin-top: 30px; }

  .head-banner__txt-inner_shadow {
    background-position: right center;
    background-size: 400px 400px; }
  /* end media min width 768 pixels */ }


