/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

#wrapper {
  min-width: 320px;
  overflow: hidden; }

.section-st {
  padding: 50px 0;
  &.section-st_first {
    padding: 60px 0 10px; }
  &.section-st_first_sm {
    padding: 60px 0 10px; } }

.container {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

.visible--lap, .hidden--full, .hidden--mob {
  display: none; }

.grid-lines-columns {
  z-index: 1;
  position: relative;
  &__field {
    display: none;
    &:before,
    &:after,
    span:before,
    span:after {
      content: ' ';
      position: absolute;
      z-index: -1;
      width: 1px;
      top: 0;
      bottom: 0;
      background-color: $cl-site-four; }
    &:before {
      left: 14px; }
    &:after {
      right: 14px; }
    span:before {
      left: 33.3%;
      margin-left: 6px; }
    span:after {
      right: 33.3%;
      margin-right: 6px; } } }

.main-grid {
  &__three-col {
    margin-bottom: 15px; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .hidden--mob {
    display: block; }

  .hidden--tabl {
    display: none; }

  .grid-lines-columns__field {
    display: block; }

  .main-grid {
    display: flex;
    flex-wrap: wrap;
    &__three-col {
      width: 50%;
      &_last {
        width: 100%; }
      &_second {
        padding: 0 10px; }
      &_first {
        padding-right: 10px; } }

    &__half-col {
      flex: 0 0 50%;
      max-width: 50%;
      &:first-child {
        padding-right: 10px; }
      &:nth-child(2n) {
        padding-left: 19px; }
      .st-video {
        min-height: 350px;
        height: 100%;
        max-width: none;
        position: relative;
        z-index: 1;
        overflow: hidden;
        width: 100%;
        >img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          min-width: 100%;
          min-height: 100%;
          max-width: none;
          z-index: -1; } } } }

  .main-grid__half-col_video-wrapp {
    padding-bottom: 30px; }

  .section-st {
    &_md {
      padding: 100px 0 60px; }
    &.section-st_first {
      padding: 90px 0 10px; }
    &.section-st_first_sm {
      padding: 90px 0 10px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-grid {
    &__half-col {
      &:first-child {
        padding-right: 65px; } } }
  .main-txt-wrapp {
    .title-site, h1, h2, h3 {
      margin-bottom: 27px; }
    p {
      margin-bottom: 35px; } }

  .main-grid {
    &__three-col {
      width: 33.3%; } }
  html, body {
    height: 100%; }

  body {
    display: flex;
    flex-direction: column; }

  #wrapper {
    flex: 1 0 auto; }

  .footer {
    flex-shrink: 0; }

  .section-st {
    padding: 50px 0;
    &_md {
      padding: 143px 0 70px; }
    &.section-st_first {
      padding: 140px 0 10px; }
    &.section-st_first_sm {
      padding: 100px 0 10px; } }

  .container {
    max-width: 980px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 auto; }

  .visible-lap {
    display: block; }

  .hidden--tabl {
    display: block; }

  .hidden-lap {
    display: none; }

  .grid-lines-columns {
    &__field {
      &:before,
      &:after,
      span:before,
      span:after {
        right: auto;
        left: 50%; }
      &:before {
        margin-left: -480px; }
      &:after {
        margin-left: 479px; }
      span:before {
        margin-left: -160px; }
      span:after {
        margin-left: 159px; } } }

  .main-grid__three-col_first {
    padding-right: 19px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {

  .section-st_lg {
    padding: 80px 0; }
  .hidden-lap {
    display: block; }
  .container {
    max-width: 1190px; }

  .grid-lines-columns {
    &__field {
      &:before {
        margin-left: -585px; }
      &:after {
        margin-left: 583px; }
      span:before {
        margin-left: -195px; }
      span:after {
        margin-left: 194px; } } }
  .main-grid__three-col_second {
    padding: 0 32px; }
  .main-grid__three-col_last {
    padding-top: 10px; }
  /* end media min width 1200 pixels */ }

/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .main-grid__half-col_video-wrapp {
    .st-video {
      min-width: 620px; } }
  /* end media min width 1330 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .main-grid__half-col_video-wrapp {
    .st-video {
      min-width: 670px; } }
  /* end media min width 1440 pixels */ }
























