/* ----- Skills ------------------- */
.skills-grid__img {
  margin-bottom: 30px; }
.skills-point {
  border: 1px solid $cl-site-four;
  background-color: $cl-site;
  padding: 10px 15px 1px;
  margin-bottom: 20px;
  p {
    font-size: 16px;
    line-height: 28px;
    letter-spacing: $lets2;
    margin-bottom: 10px; }
  &-cap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
    &__value {
      color: #a4a4a4;
      font-size: 58px;
      font-weight: 700;
      line-height: 1;
      flex-shrink: 0;
      margin-right: 10px; }
    &__name {
      color: $cl-site-three;
      font-size: 16px;
      font-weight: 600;
      line-height: 18px;
      letter-spacing: $lets2;
      flex-grow: 1;
      text-align: right; } } }

.skills-values {
  margin-left: -15px;
  margin-right: -15px;
  text-align: center;
  &__item {
    padding-left: 15px;
    padding-right: 15px;
    display: inline-block;
    vertical-align: top;
    width: 240px;
    margin-bottom: 30px; } }

.skills-value {
  display: block;
  text-decoration: none;
  outline: none;
  &__tt {
    color: $cl-site-three;
    font-size: 48px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    margin-bottom: 10px; }
  &__name {
    color: $cl-light;
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
    text-transform: uppercase;
    letter-spacing: 0.45px;
    margin-bottom: 15px; }
  p {
    color: $cl-light;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: $lets2; } }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .skills-points {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-right: -1%;
    margin-left: -1%;
    .skills-point {
      width: 48%;
      margin-right: 1%;
      margin-left: 1%; } }

  .skills-value {
    &__name {
      min-height: 44px; } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .skills-points {
    margin-right: -1%;
    margin-left: -1%;
    .skills-point {
      width: 31.3%;
      margin-right: 1%;
      margin-left: 1%;
      &:nth-child(3n+2), &:nth-child(2) {
        width: 33.3%;
        margin-left: 0;
        margin-right: 0; } } }

  .skills-values__item_hidden-tabl {
    display: none; }
  .skills-values {
    margin-top: 120px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .skills-values__item_hidden-tabl {
    display: inline-block; }
  .skills-values {
    margin-top: 180px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .skills-values {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 0;
    margin-right: 0; }
  .skills-grid {
    display: flex;
    justify-content: flex-start;
    &__img {
      margin-right: 46px;
      flex: 0 0 310px;
      max-width: 310px;
      display: flex;
      flex-direction: column;
      margin-bottom: 0;
      padding-bottom: 20px;
      .img-br {
        height: 100%;
        min-height: 100%;
        position: relative;
        >img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          min-width: 100%;
          min-height: 100%;
          max-width: none; } } }

    &__points {
      flex-grow: 1; } }

  .skills-points {
    margin-right: -1%;
    margin-left: 0;
    .skills-point {
      width: 31.3%;
      margin-right: 1%;
      margin-left: 1%;
      &:nth-child(3n+2), &:nth-child(2) {
        width: 31.3%;
        margin-right: 1%;
        margin-left: 1%; } } }

  .skills-point-cap {
    margin-bottom: 26px; }
  .skills-point {
    padding-bottom: 10px; }

  .section-st_skills-bg {
    background: url(../img/skills-bg.png) 50% 80% no-repeat; }

  .section-st_skills {
    padding: 90px 0;
    &-wrapp {
      padding: 70px 0; } }
  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .skills-grid__img .img-br {
    margin-left: -50px;
    width: auto;
    max-width: none; }

  .section-st_skills-wrapp {
    .main-tt__description_right {
      right: -50px; } }

  /* end media min width 1330 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .skills-grid__img .img-br {
    margin-left: -100px; }

  .section-st_skills-wrapp {
    .main-tt__description_right {
      right: -100px; } }
  /* end media min width 1440 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .skills-grid__img .img-br {
    margin-left: -180px; }

  .section-st_skills-wrapp {
    .main-tt__description_right {
      right: -150px; } }
  /* end media min width 1600 pixels */ }

/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .skills-grid__img .img-br {
    margin-left: -280px;
    &:after {
      left: 40px;
      top: 35px;
      bottom: 35px;
      right: 40px; } }

  .section-st_skills-wrapp {
    .main-tt__description_right {
      right: -220px; } }
  /* end media min width 1800 pixels */ }

