/* ----- Index page -------------------- */
.btn-wrapp {
  margin-top: 40px; }

.main-tt {
  text-align: center;
  margin-bottom: 35px;
  position: relative;
  p {
    margin-top: 25px; }
  &_left {
    text-align: left; }

  .title-site, h1, h2, h3 {
    margin-bottom: 0;
    position: relative;
    display: inline-block; }
  &__description {
    display: none;
    left: 0;
    position: absolute;
    bottom: -40px;
    color: $cl-mine-shaft;
    font-size: 50px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
    z-index: -1;
    letter-spacing: 2.75px;
    &_right {
      right: -10px;
      left: auto;
      text-align: right; }
    &_center.main-tt__description {
      left: 50%;
      transform: translateX(-50%);
      width: 100%; } }

  &_line-left {
    .title-site, h1, h2, h3 {
      &:before {
        content: ' ';
        position: absolute;
        height: 5px;
        width: 22px;
        right: 100%;
        margin-right: 20px;
        bottom: 7px;
        background: url(../img/line-left.png) left center no-repeat; } } }

  &_line-right {
    .title-site, h1, h2, h3 {
      &:after {
        content: ' ';
        position: absolute;
        height: 5px;
        width: 22px;
        left: 100%;
        margin-left: 20px;
        bottom: 7px;
        background: url(../img/line-right.png) right center no-repeat; } } } }

.main-tt_rotate {
  .title-site, h1, h2, h3 {
    &:before,
    &:after {
      transform: rotate(180deg); } } }

.txt-project {
  max-width: 690px; }

.project-box {
  margin-bottom: 60px; }



/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  .card-list {
    display: flex;
    flex-wrap: wrap;
    &__item {
      flex: 0 0 50%;
      max-width: 50%; } }
  /* end media min width 600 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .main-tt {
    margin-bottom: 40px;
    &_md {
      margin-bottom: 60px; }
    &_lg {
      margin-bottom: 80px; }
    &_line-left {
      .title-site, h1, h2, h3 {
        &:before {
          width: 122px;
          margin-right: 40px; } } }
    &_line-right {
      .title-site, h1, h2, h3 {
        &:after {
          width: 122px;
          margin-left: 40px; } } } }

  .main-tt__description {
    display: block;
    bottom: -100px;
    font-size: 80px;
    &_sm {
      font-size: 70px;
      bottom: -80px; } }

  .btn-wrapp, .btn-wrapp_sm.btn-wrapp {
    margin-top: 60px; }
  .project-box {
    .main-tt__description {
      bottom: -80px; } }

  .main-tt_left {
    .main-tt__description {
      bottom: -40px;
      left: auto;
      right: 0;
      font-size: 40px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .card-list {
    display: flex;
    flex-wrap: wrap;
    &__item {
      flex: 0 0 33.3%;
      max-width: 33.3%; } }
  .main-tt {
    margin-bottom: 96px;
    &_sm {
      margin-bottom: 80px; }
    &_md {
      margin-bottom: 100px; }
    &_lg {
      margin-bottom: 110px; }
    .title-site, h1, h2, h3 {
      letter-spacing: $lets1; } }
  .main-tt_line-left .title-site:before {
    width: 242px;
    margin-right: 110px; }
  .main-tt_line-right .title-site:after {
    width: 242px;
    margin-left: 110px; }

  .main-tt__description {
    bottom: -150px;
    &_right {
      bottom: -140px; }
    &_sm {
      bottom: -100px; } }

  .btn-wrapp {
    margin-top: 90px; }
  .btn-wrapp_sm.btn-wrapp {
    margin-top: 80px; }
  .project-box {
    .main-tt__description {
      bottom: -100px; } }
  .main-tt_small.main-tt {
    margin-bottom: 30px; }
  .main-tt_left.main-tt {
    .main-tt__description {
      bottom: auto;
      top: 50%;
      transform: translateY(-50%);
      font-size: 60px; } }

  .main-tt__description.main-tt__description_center-column {
    bottom: -110px; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-tt_line-left {
    .title-site, h1, h2, h3 {
      &:before {
        margin-right: 214px; } } }
  .main-tt_line-right {
    .title-site, h1, h2, h3 {
      &:after {
        margin-right: 214px;
        margin-left: 200px; } } }

  .main-tt__description {
    font-size: 110px;
    bottom: -170px;
    &_right {
      bottom: -150px; }
    &_sm {
      font-size: 90px;
      bottom: -110px; } }

  .section-st_about-bg {
    background: url(../img/about-bg.png) 0 80% no-repeat; }

  .main-tt {
    position: relative;
    .title-site {
      position: static; } }
  .main-tt_line-left {
    .title-site, h1, h2, h3 {
      &:before {
        right: auto;
        left: 15px;
        margin-left: 0;
        margin-right: 0; } } }
  .main-tt_line-right {
    .title-site, h1, h2, h3 {
      &:after {
        left: auto;
        right: 15px;
        margin-left: 0;
        margin-right: 0; } } }
  .main-tt_center {
    .title-site, h1, h2, h3 {
      position: relative;
      &:before {
        left: auto;
        right: 100%;
        margin-right: 118px; }
      &:after {
        left: 100%;
        margin-left: 118px; } }
    &_sm {
      .title-site, h1, h2, h3 {
        &:before {
          margin-right: 58px; }
        &:after {
          margin-left: 58px; } } } }
  .project-box {
    .main-tt__description {
      bottom: -210px;
      right: -50px; } }
  .main-tt_left.main-tt {
    .main-tt__description {
      font-size: 72px;
      right: 65px; } }

  /* end media min width 1200 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .main-tt__description {
    left: -40px; }
  .card-list {
    &__item {
      flex: 0 0 25%;
      max-width: 25%; } }
  .project-box {
    .main-tt__description {
      right: -80px; } }
  /* end media min width 1330 pixels */ }

/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .main-tt__description {
    left: -120px; }
  .project-box {
    .main-tt__description {
      right: -130px; } }
  /* end media min width 1440 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .main-tt__description {
    left: -150px; }
  .project-box {
    .main-tt__description {
      right: -220px; } }
  /* end media min width 1600 pixels */ }

/* ----- END index page /--------------- */
