/* ----- Contacts Page ------------------- */
.c-grid__img {
  margin-bottom: 40px; }

.c-icon-bl {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  &__icon {
    border: 1px dashed $cl-site-three;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    flex-shrink: 0;
    margin-right: 20px; }
  &__content {
    flex-grow: 1;
    p, a {
      color: $cl-light;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: $lets2; }
    a {
      text-decoration: none;
      outline: none;
      display: inline-block;
      margin-bottom: 20px; }
    a:hover {
      color: $cl-site-three;
      text-decoration: none; } } }

.c-name {
  color: $cl-light;
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
  letter-spacing: 0.6px;
  margin-bottom: 10px; }

.section-st_contacts.section-st_first {
  background: url(../img/contacts-bg.png) center bottom no-repeat;
  padding-bottom: 100px;
  background-size: 100% auto;
  position: relative;
  &:before {
    content: ' ';
    position: absolute;
    background: url(../img/contacts-bg2.png) left top no-repeat;
    background-size: contain;
    top: 0;
    left: 0;
    width: 798px;
    height: 393px;
    max-width: 100%;
    z-index: -1; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .c-icon-bl {
    &__content {
      p, a {
        font-size: 16px;
        line-height: 28px; } } }

  .c-name {
    font-size: 24px;
    line-height: 26px; }

  .c-grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    &__img {
      flex: 0 0 48%;
      order: 1;
      .img-br {
        position: relative;
        overflow: hidden;
        min-height: 400px;
        max-width: none;
        width: 100%;
        >img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          min-width: 100%;
          min-height: 100%;
          max-width: none; } } }

    &__icons {
      flex: 0 0 48%; } }

  .c-icon-bl {
    margin-bottom: 5px; }


  .section-st_contacts.section-st_first {
    &:before {
      max-width: 500px; } }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .c-icon-bl__icon {
    width: 70px;
    height: 70px;
    line-height: 70px;
    margin-bottom: 40px; }

  .c-icon-bl {
    &__icon {
      margin-right: 30px; }
    &__content {
      padding-top: 8px; } }

  .c-grid__img {
    margin-bottom: 0; }

  .c-grid {
    margin-bottom: 50px; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .c-grid {
    &__img {
      flex: 0 0 55%;
      .img-br {
        min-height: 480px;
        &:after {
          left: 35px;
          right: 35px;
          top: 35px;
          bottom: 35px; } } }

    &__icons {
      flex: 0 0 42%;
      .c-icon-bl {
        &:last-child {
          margin-bottom: 20px; } } } }

  .section-st_contacts.section-st_first {
    &:before {
      top: 80px;
      left: 20px;
      max-width: 500px; } }
  /* end media min width 1200 pixels */ }


/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .section-st_contacts.section-st_first {
    &:before {
      max-width: none; } }
  /* end media min width 1600 pixels */ }

/* ----- END Contacts Page --------------- */
