/* ----- Forms ------------------------- */
.st-form {
  margin-bottom: 20px;

  &__tt {
    display: block; } }


.form-group {
  margin-bottom: 30px;

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0; } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  .form-group__label {
    order: 1; } }

.form-contact__cap {
  padding-top: 30px;
  position: relative;
  text-align: center;
  margin-bottom: 35px;
  &:before {
    content: ' ';
    width: 100px;
    margin-left: -50px;
    left: 50%;
    top: 0;
    height: 1px;
    position: absolute;
    background-color: #888; }
  .form-contact__tt {
    margin-bottom: 10px; } }

.form-contact__tt {
  color: $cl-light;
  font-size: 22px;
  line-height: 32px;
  font-weight: 700;
  letter-spacing: $lets1;
  text-transform: uppercase;
  margin-bottom: 20px; }
.form-contact__txt {
  color: $cl-light;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.55px;
  margin-bottom: 20px; }

.form-group_btn {
  margin-top: 40px; }

.form-group__textarea {
  height: 85px;
  padding-top: 55px;
  line-height: 18px;
  padding-bottom: 10px; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-contact__tt {
    font-size: 30px;
    line-height: 32px; }
  .form-contact__txt {
    font-size: 22px;
    line-height: 24px; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .form-contact {
    .form-group {
      margin-bottom: 36px; }
    .form-group_btn {
      margin-top: 80px; } }
  /* end media min width 1000 pixels */ }


/* ----- END Forms --------------------- */
