/* ----- Card ------------------- */
.card {
  display: block;
  overflow: hidden;
  z-index: 1;
  position: relative;
  text-decoration: none;
  outline: none;
  min-height: 450px;
  color: #fff;
  &:hover {
    &:after {
      opacity: 0; }
    .card-txt {
      opacity: 0; } }

  >img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    max-width: none;
    z-index: -2;
    transition: ease 0.2s; }
  &:hover {
    text-decoration: none;
    outline: none;
    >img {
      transform: translate(-50%, -50%) scale(1.05); }
    &:before {
      opacity: 1;
      transform: rotateX(0); } }
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -80px;
    margin-top: -80px;
    width: 159px;
    height: 159px;
    border-radius: 50%;
    background: rgba($cl-dark, 0.4) url(../img/icons/look.png) center center no-repeat;
    transition: ease 0.2s;
    transform: rotateX(90deg); }

  &:after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(#000, 0.5);
    z-index: -1;
    transition: ease-in-out 0.2s;
    opacity: 1; } }

.main-card-slider {
  perspective: 1000px; }

.card-txt {
  position: absolute;
  transition: ease-in-out 0.2s;
  opacity: 1;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  &__top {
    color: #f7f7f7;
    font-size: 20px;
    font-weight: 700;
    line-height: 32px;
    text-transform: uppercase;
    letter-spacing: 0.5px; }
  &__left {
    transform: rotate(-90deg);
    transform-origin: left bottom;
    position: absolute;
    left: 20px;
    bottom: 0;
    color: #f7f7f7;
    font-size: 10px;
    font-weight: 500;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 1.6px; }
  &__bottom {
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: right;
    left: 50px;
    color: $cl-light;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: $lets2; } }

.card-list__item_mob-hidden, .card-list__item_lap-hidden {
  display: none; }


/* Min width 600 pixels */
@media only screen and (min-width: 600px) {
  /* end media min width 600 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {

  .main-card-slider.nav-st {
    .main-slick-prev {
      left: 20px; }
    .main-slick-next {
      right: 20px; } }

  .card {
    min-height: 600px; }
  .card-txt {
    &__left {
      font-size: 16px; } }
  .card-list__item_mob-hidden {
    display: block; }
  /* end media min width 1000 pixels */ }


/* Min width 1330 pixels */
@media only screen and (min-width: 1330px) {
  .card-list__item_lap-hidden {
    display: block; }
  /* end media min width 1330 pixels */ }

/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .card {
    min-height: 880px; }
  .card-txt {
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px; }
  .main-card-slider.nav-st {
    .main-slick-prev {
      left: 40px; }
    .main-slick-next {
      right: 40px; } }
  /* end media min width 1600 pixels */ }


