/* ----- Footer ------------------------ */
.footer {
  padding: 30px 0 15px;
  background-color: $cl-site;
  border-top: 1px solid $cl-site-four; }

.f-menu {
  text-align: center;
  li {
    color: $cl-alto;
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: $lets2;
    margin-bottom: 15px; }
  a {
    text-decoration: none;
    color: $cl-alto;
    &:hover {
      text-decoration: none;
      color: darken($cl-alto, 20%); } } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .f-menu {
    li {
      display: inline-block;
      vertical-align: middle;
      margin: 0 10px 15px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .footer {
    padding: 34px 0 19px; }
  .f-menu {
    li {
      margin: 0 5px 15px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .f-menu {
    li {
      margin: 0 30px 15px; } }
  /* end media min width 1200 pixels */ }

/* ----- END Footer -------------------- */
